import React, { Component, useState } from 'react';

import TextField from '@material-ui/core/TextField';
import { makeStyles } from '@material-ui/core/styles';

import { getDate } from '../utils';
import { LoadingMessage } from '../CommonComponents';


const wsCardStyles = makeStyles({
  root: {
    minWidth: 240,
    minHeight: 120,
  },
  hovered: {
    minWidth: 240,
    opacity: 0.1,
  },
});

function WsCardContent ({ws: ws}){
  return (
    <div className='content'>
      <h5>{ws.ws_name}</h5>
      <p className='mb-0 font-14'>Кол-во пересчетов в клетках: 
        <span className='color-mainDark-dark font-600 ml-1'>{ws.recounts_balance_count}</span></p>
      {ws.recounts_balance_count 
        ? ws.recounts_balance_sum > 0
            ? <span>
                <span className='color-red1-light font-550'>Уменьшили </span>
                  количество на 
                <span className='color-mainDark-dark font-14 font-600 ml-1'>
                  {ws.recounts_balance_sum}
                </span>
              </span>
            : <span>
                <span className='color-green1-light font-550'>Увеличили </span>количество на 
                <span className='color-mainDark-dark font-14 font-600 ml-1'>
                  {Math.abs(ws.recounts_balance_sum)}
                </span>
              </span>
        : <p className='mb-0'>Пересчетов нет</p>
      }
    </div>
  )
}

function WsCard({ws: ws, showSections: showSections}) {
  const classes = wsCardStyles();
  const [isShown, setIsShown] = useState(false);

  function handleClick () {
    if (ws.recounts_balance_count > 0)
      showSections(ws.ws_number)
  }

  return (
    <div className={(isShown && ws.recounts_balance_count > 0) 
        ? classes.root + ' card card-style bg-mainDark-dark ' : classes.root + ' card card-style'} 
         onClick={handleClick}
         onMouseEnter={() => setIsShown(true)}
         onMouseLeave={() => setIsShown(false)}>
      {!isShown
        ? <WsCardContent ws={ws} />
        : ws.recounts_balance_count > 0
          ? <div className='content'>
              <div className='d-flex justify-content-center align-content-center'>
                <h5 className='color-white'>Подробнее</h5>
              </div>
            </div>
          : <WsCardContent ws={ws} />
      }
    </div>
  );
}


class RecountReportComponent extends Component {
  constructor(props) {
    super(props);
    this.state = {
      start_date: null,
      end_date: null,
    }
    this.setData = this.setData.bind(this);
    this.showAllWsRecounts = this.showAllWsRecounts.bind(this);
    this.showSections = this.showSections.bind(this);
	}

  componentDidMount() {
    const { date_after, date_before } = getDate()
    this.props.getRecountWssReport({start_date: date_after, end_date: date_before})
    this.setState({
      ...this.state,
      start_date: date_after,
      end_date: date_before,
    })
  }

  setData (e) {
    this.setState({
      ...this.state,
      [e.target.name]: e.target.value
    })
  }

  showAllWsRecounts () {
    const { start_date, end_date } = this.state
    this.props.getRecountWssReport({start_date: start_date, end_date: end_date})
  }

  showSections (ws_number) {
    const { start_date, end_date } = this.state
    this.props.getRecountWsReport({start_date: start_date, end_date: end_date, ws_number: ws_number})
  }

  render() {
    const { wssReport, wsReport, reportsFetching } = this.props
    return (
      <div>
        <div className="card card-style my-3">
          <div className='content'>
            <h3 className='text-center mb-2'>Пересчет животных за период</h3>
            <div className="d-flex justify-content-around">
                <TextField
                  label={'Дата с'}
                  type="date"
                  name='start_date'
                  className='mr-2'
                  value={this.state.start_date}
                  onChange={this.setData}
                  InputLabelProps={{
                    className: '',
                    shrink: true 
                  }}
                />
                <TextField
                  label={'Дата до'}
                  type="date"
                  name='end_date'
                  className='mr-2'
                  value={this.state.end_date}
                  onChange={this.setData}
                  InputLabelProps={{
                    className: '',
                    shrink: true 
                  }}
                />

              <button className='btn bg-mainDark-dark ' onClick={this.showAllWsRecounts}>
                Показать
              </button>
            </div>
          </div>
          
        </div>
        {wssReport.length > 0 && 
          <div className='d-flex justify-content-between flex-wrap'>
            {wssReport.map(ws => 
              <WsCard ws={ws} showSections={this.showSections}/>
              )}
          </div>
        }
        {reportsFetching && <LoadingMessage />}
        {wsReport.length > 0 && 
          <div className='card card-style'>
            <div className='content'>
              {wsReport.map(section =>
                <div>
                  <h5>Секция {section.number}</h5>
                  <p className='mb-0'>Общее количество 
                    {section.balance === 0 && <span className='font-italic'> без изменений.</span>}
                    {section.balance > 0
                      ? <span>
                          <span className='color-red1-light font-600'> уменьшили </span>
                            на 
                          <span className='color-mainDark-dark font-14 font-600 ml-1'>
                            {section.balance}
                          </span>
                        </span>
                      : section.balance !== 0 && 
                        <span>
                          <span className='color-green1-light font-600'> увеличили </span> на 
                          <span className='color-mainDark-dark font-14 font-600 ml-1'>
                            {Math.abs(section.balance)}
                          </span>
                        </span>
                    }
                  </p>
                  {section.recounts.length > 0 
                    ? <table className='table table-sm'>
                        <thead>
                          <th>Дата</th>
                          <th>Клетка</th>
                          <th>Сколько было</th>
                          <th>Сколько стало</th>
                          <th>Итог</th>
                          <th>Сотрудник</th>
                          <th>Комментарий</th>
                        </thead>
                        <tbody>
                          {section.recounts.map(recount => 
                            <tr>
                              <td>{recount.date}</td>
                              <td>{recount.cell}</td>
                              <td>{recount.quantity_before}</td>
                              <td>{recount.quantity_after}</td>
                              <td>
                                {recount.balance > 0 
                                  ? <span className='color-red1-light font-600'>-{recount.balance}</span>
                                  : <span className='color-green1-light font-600'>
                                      +{Math.abs(recount.balance)}</span>
                                }
                              </td>
                              <td>{recount.initiator}</td>
                              <td>{recount.note}</td>
                            </tr>
                          )}
                        </tbody>
                      </table>
                    : <span>нет пересчетов</span>
                  }
                </div>
                )}
            </div>
          </div>
        }
      </div>
    );
  }
}


export default RecountReportComponent